#main {
  display: flex;
  background-image: url(../img/andrew-christine-engagement-12-09-2017.jpg);
  background-repeat: no-repeat;
  background-position: 70% 0;
  flex-direction: column;
  min-height: 100vh;
  padding: 2em;
  color: white; }
  @media screen and (max-width: 650px) {
    #main {
      background-image: url(../img/andrew-christine-engagement-12-09-2017-650.jpg);
      background-position: 60% 0; } }
  @media screen and (max-width: 450px) {
    #main {
      background-image: url(../img/andrew-christine-engagement-12-09-2017-450.jpg); } }
  @media screen and (min-width: 1920px) {
    #main {
      background-image: url(../img/andrew-christine-engagement-12-09-2017-2560.jpg);
      background-position: 50% 0; } }
  #main, #main h1, #main h2, #main h3, #main h4, #main h5, #main h6 {
    font-family: 'Shadows Into Light Two', cursive; }
  #main hr {
    width: 100%; }

#announcement {
  font-family: 'Shadows Into Light Two', cursive;
  font-size: 2em;
  border: 3px solid rgba(255, 255, 255, 0.5);
  padding: 1em;
  text-align: center;
  position: relative;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.65); }
  @media screen and (max-width: 650px) {
    #announcement {
      font-size: 1.5em; } }
  @media screen and (max-width: 450px) {
    #announcement {
      font-size: 1em; } }
  @media screen and (min-width: 1920px) {
    #announcement {
      font-size: 3em; } }
  #announcement .text {
    display: flex;
    align-items: center;
    justify-content: center; }
  #announcement hr {
    display: block;
    border-top: solid 3px rgba(255, 255, 255, 0.5); }
